.resultContainer{
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-columns: 1fr 2fr;
    width: 342px;
    height: 193px;
    padding: 10px;
    border-radius: 4px;
    background-color: #282623;
    box-shadow: 0 0 0 1px #d9d9d9 inset;
    border: none;
}

.resultText{
    width: 100%;
    height: 100%;
    padding: 20px 10px;
    font-size: 1.4rem;
    text-align: left;
    color: #808080;
    overflow: hidden;
}

.resultText h3{
    color: #FFFF;
    font-size: 1.9rem;
}

.resultText p{
    margin-top: 5px;
}   

.availableLabel{
    margin-top: 10px !important;
    font-size: 1.5rem;
    padding: 5px;
    border-radius: 4px;
    width: fit-content;
}

